<template>
  <ul class="nav" v-if="showNav">
    <li><router-link class="btn" :to="{name: 'index'}">Games overview</router-link></li>
    <li><router-link class="btn" :to="{name: 'new-game'}">New game</router-link></li>
    <li><a href="https://stand-with-ukraine.pp.ua/" class="btn btn-ukraine" target="_blank">
      <icon icon="ukraine-heart" /> Stand with Ukraine <icon icon="ukraine-heart" /></a></li>

    <li style="float:right">
      <span class="btn" v-if="loggedIn" @click="doLogout">Logout</span>
      <span v-else class="btn" @click="openLoginDialog">Login</span>
    </li>
    <!-- <li style="float:right"><router-link class="btn" :to="{name: 'admin'}">Admin</router-link></li> -->
  </ul>

  <LoginDialog
    v-if="loginDialogOpen"
    @close="loginDialogOpen=false"
    @bgclick="loginDialogOpen=false" />
</template>
<script setup lang="ts">
import { computed, onMounted, ref } from 'vue'
import { useRoute } from 'vue-router';
import LoginDialog from './LoginDialog.vue';
import user from '../user'

const loginDialogOpen = ref<boolean>(false);

const loggedIn = ref<boolean>(false);
const route = useRoute();
const showNav = computed(() => {
  // TODO: add info wether to show nav to route props
  return !['game', 'replay'].includes(String(route.name))
})

async function doLogout() {
  await user.logout()
}

const openLoginDialog = () => {
  loginDialogOpen.value = true
}
onMounted(async () => {
  loggedIn.value = user.getMe()?.loggedIn
  user.eventBus.on('login', () => {
    console.log('login')
    loggedIn.value = true
    loginDialogOpen.value = false
  })
  user.eventBus.on('logout', () => {
    console.log('logout')
    loggedIn.value = false
  })
})
</script>
