<template>
  <overlay class="login-dialog">
    <template v-slot:default>
      <h2>Login</h2>
      <div>Name: <input type="text" v-model="login" /></div>
      <div>Pass: <input type="password" v-model="pass" /></div>
      <span class="btn" @click="doLogin">Login</span>
    </template>
  </overlay>
</template>
<script setup lang="ts">
import { ref } from 'vue';
import user from '../user';

const login = ref<string>('')
const pass = ref<string>('')

async function doLogin() {
  await user.login(login.value, pass.value)
}
</script>
